:root {
    --main-color:#00B3B3;
}

* {
 font-family: Arial;
}

body {
  margin-left: auto;
  margin-right: auto;
  max-width: 50%;
}

@media only screen and (max-width: 768px) {
body {
  margin-left: auto;
  margin-right: auto;
  max-width: 90%;
}
}

a {
  color: var(--main-color);
}

a:visited {
  color: var(--main-color);
}

blockquote {
  border-left: medium double var(--main-color);
  padding-left: 7px;
  font-style: italic;
}

li {
  list-style: none;
}

ul li::before {
  content: "♦";  
  color: var(--main-color); /* Change the color */
  font-weight: bold; /* If you want it to be bold */
  display: inline-block; /* Needed to add space between the bullet and the text */
  width: 1em; /* Also needed for space (tweak if needed) */
  margin-left: -1em; /* Also needed for space (tweak if needed) */
}

hr {
  margin-top: 30px;
  margin-bottom: 30px;
  border: 0;
  border-top: medium double var(--main-color);
  height: 1px;
  overflow: visible;
  padding: 0;
  color: var(--main-color);
  text-align: center;
}

hr::after {
  content: "♦";
  display: inline-block;
  position: relative;
  top: -0.7em;
  font-size: 1.4em;
  padding: 0 0.3em;
  background: white;
}

.menu {
  background-color: white;
  overflow: hidden;
  margin-top: 30px;
  border: medium double var(--main-color);
}

.menu a {
  float: left;
  color: var(--main-color);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.menu a:hover {
  background-color: #00B3B3;
  color: white;
}

.menu a:current {
  background-color: #00B3B3;
  color: white;
}